* {
}
*,
*:before,
*:after {
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
}

aside,
nav,
footer,
header,
section {
	display: block;
	padding: 0px;
	margin: 0px;
	border: 0px;
}
html,
body {
	scroll-behavior: smooth;
	height: 100%;
	position: relative;
	padding: 0px;
	margin: 0px;
	border: 0px;
}
input {
	padding: 0px;
	margin: 0px;
	border: 0px;
}
body {
	line-height: 1;
	//text-rendering: optimizeLegibility;
	-ms-text-size-adjust: 100%;
	-moz-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
}
input,
button,
textarea {
	padding: 0px;
	margin: 0px;
	resize: none;
	border: 0px;
}
button {
	background-color: transparent;
}
input::-ms-clear {
	display: none;
}
button {
	cursor: pointer;
	padding: 0px;
	margin: 0px;
	border: 0px;
}
button::-moz-focus-inner {
	padding: 0;
	border: 0;
}
a,
a:visited {
	text-decoration: none;
	padding: 0px;
	margin: 0px;
	border: 0px;
}
a:hover {
	text-decoration: none;
}
ul li {
	list-style: none;
}

ul,
li,
p {
	padding: 0px;
	margin: 0px;
	border: 0px;
}

img {
	vertical-align: top;
	padding: 0px;
	margin: 0px;
	border: 0px;
}
h1,
h2,
h3,
h4,
h5,
h6 {
	font-weight: inherit;
	font-size: inherit;
	padding: 0px;
	margin: 0px;
	border: 0px;
}
div {
	padding: 0px;
	margin: 0px;
	border: 0px;
}
